<script>
export default {
  name: "gourmet",
  data() {
    return {
      list: [
        {
          img: require('@/assets/images/餐食配图/贝果系列/1.jpeg'),
          en: {
            content: 'Lobster bagel with black bean sauce flavor',
            series: "BAGELS"
          },
          sc: {
            content: '豉香龙虾贝果',
            series: "贝果"
          },
          tc: {
            content: ' 豉香龍蝦貝果 ',
            series: "貝果"
          }
        },
        {
          img: require('@/assets/images/餐食配图/餐包系列/1.jpg'),
          en: {
            content: 'Orange-scented scone',
            series: "BREAD · ROLL"
          },
          sc: {
            content: '橙香司康',
            series: "餐包"
          },
          tc: {
            content: ' 橙香司康 ',
            series: "餐包"
          }
        },
        {
          img: require('@/assets/images/餐食配图/餐包系列/2.jpeg'),
          en: {
            content: 'Alkaline water cheese stick',
            series: "BREAD · ROLL"
          },
          sc: {
            content: '碱水乳酪棒',
            series: "餐包"
          },
          tc: {
            content: ' 堿水乳酪棒 ',
            series: "餐包"
          }
        },
        {
          img: require('@/assets/images/餐食配图/蛋糕系列/1.jpg'),
          en: {
            content: 'Basque cheesecake',
            series: "PASTRY"
          },
          sc: {
            content: '巴斯克芝士蛋糕',
            series: "蛋糕"
          },
          tc: {
            content: ' 巴斯克芝士蛋糕 ',
            series: "蛋糕"
          }
        },
      ],
      filterList: [
        {
          id: 1,
          en: 'All Series',
          sc: "全系列",
          tc: "全系列"
        },
        {
          id: 2,
          en: 'BAGELS',
          sc: "贝果",
          tc: "貝果"
        },
        {
          id: 3,
          en: 'BREAD · ROLL',
          sc: "餐包",
          tc: "餐包"
        },
        {
          id: 4,
          en: 'PASTRY',
          sc: "蛋糕",
          tc: "蛋糕"
        }
      ],
      curFilterValue: {},
      showList: []
    }
  },
  methods: {
    selectSeries(obj) {
      this.curFilterValue = obj;
      if (obj.id === 1) {
        this.showList = this.list;
      } else {
        this.showList = this.list.filter(item => item[this.$i18n.locale].series === obj[this.$i18n.locale])
      }
    }
  },
  mounted() {
    this.showList = this.list;
    this.curFilterValue = this.filterList[0]
  }
}
</script>

<template>
  <div class="w-full">
    <div class="container mx-auto pt-10">
      <div class="h-96 flex flex-col items-center justify-center my-16 animate__animated animate__fadeInUp">
        <p class="font-bold sm:text-5xl text-3xl">{{ $t('coffee.gourmet.title') }}</p>
        <p class="text-sm my-5 sm:w-1/2 w-3/4 leading-6">
          {{ $t('coffee.gourmet.content') }}
        </p>
        <el-dropdown @command="selectSeries">
          <div class="flex items-center shadow-xl hover:shadow-2xl transition-all px-3 py-2 cursor-pointer">
            <span>{{ $t('coffee.gourmet.button.title') }}:</span>
            <span
                class="text-coffee ml-2 underline hover:text-coffeeLight transition-all">{{
                curFilterValue[$i18n.locale]
              }}</span>
          </div>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-for="item in filterList" :key="item.id" :command="item">
              <p :class="{'text-coffeeLight': item[$i18n.locale] === curFilterValue[$i18n.locale]}">{{
                  item[$i18n.locale]
                }}</p>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>

      </div>
      <div class="w-full flex flex-wrap mb-32 sm:px-20 animate__animated animate__fadeInUp animate__delay-1s">
        <div v-for="item in showList" :key="item.id" class="sm:w-1/4 w-1/2 relative flex-shrink-0 px-3 box-border mt-5">
          <div class="w-full h-52 md:h-40 lg:h-52 overflow-hidden">
            <img class="w-full h-full cursor-pointer hover:scale-110 transition transform"
                 :src="item.img" alt="">
          </div>
          <p class="mt-1 text-coffee font-bold text-sm">{{ item[$i18n.locale].content }}</p>
          <div class="absolute top-1 right-6 text-once text-xs w-2 mx-auto text-center">
            {{ item[$i18n.locale].series }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped lang="scss">
.text-once {
  color: white;
  -webkit-text-stroke: 1px #BC9B5D;
  word-wrap: break-word; /*英文的时候需要加上这句，自动换行*/
}
</style>